let config = {
	//,
	whiteList: ['/pages/login/login', '/pages/login/register', '/pages/login/forget-password',
		'/pages/index/index', '/pages/index/contact', '/pages/index/guide', '/pages/case/list',
		'/pages/case/detail', '/pages/voucher/list', '/pages/voucher/new'
	],
	loginPage: '/pages/login/login'
}

// 1H5,





















///2uniapp  navigateBack
let list = ["navigateTo", "redirectTo", "reLaunch", "switchTab"];
list.forEach(item => { //,uni.navigateTo,uni.redirectTo,uni.reLaunch,uni.switchTab4
	uni.addInterceptor(item, {
		invoke(e) { // 
			const token = uni.getStorageSync('token'), //token				
				url = e.url.split('?')[0]; //url"?""?"		
			let notNeed = config.whiteList.includes(url)
			// whiteList
			if (notNeed) {
				return e
			} else {
				if (token == '' || token == 'false' || token == null) {
					uni.showModal({
						title: '',
						content: '',
						cancelText: '', // 
						confirmText: "", // 
						success(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: config.loginPage
								});
							}
						}
					})
					return false
				} else {
					return e
				}
			}

		},
		fail(err) { //  
			console.log(err);
		}
	})
})
