import {
	refreshToken
} from '@/api/user.js'
import {
	baseIpPort
} from '@/static/json/base-url.js'

let socketUrl = ''; // socket
let socketTask = null; // socketTask 
let is_open_socket = null; // 
let heartTime = null; // 
let socketHeart = 0; // 
const heartTimeOut = 5000; //  10000 = 10s
let connectNum = 0; // 

// socket
export const initWebSocket = (url) => {
	if (!url) {
		let protocol = window.location.protocol === 'https:' ? 'wss://' : 'ws://';
		url = protocol + baseIpPort + '/resource/websocket'
	}
	socketUrl = url;
	let token = uni.getStorageSync('token');
	let user = uni.getStorageSync('user');
	//  websocket
	socketTask = uni.connectSocket({
		url: url + '?Authorization=Bearer ' + token + '&clientid=' +
			user.id,
		header: {
			'content-type': 'application/json'
			// Authorization: 'Bearer ' + uni.getStorageSync('token')
		},
		method: 'GET',
		success(res) {
			console.log(res);
			return socketTask;
		},
		fail(err) {
			console.log(err);
		}
	});
	websocketonopen();
	websocketonmessage();
	websocketonerror();
	websocketclose();
	sendSocketHeart();
};

// socket 
export const websocketonopen = () => {
	socketTask.onOpen(function(res) {
		console.log('WebSocket');
		is_open_socket = true;
		resetHeart();
	});
};

// socket 
export const websocketonerror = () => {
	socketTask.onError(function(res) {
		console.log('WebSocket', res);
		socketTask = null;
		is_open_socket = false;
		// 
		reconnect();
	});
};

// socket 
export const websocketclose = () => {
	socketTask.onClose(function(res) {
		console.log('WebSocket ', res);
	});
};

// socket 
export const resetHeart = () => {
	socketHeart = 0;
	connectNum = 0;
	clearInterval(heartTime);
	sendSocketHeart();
};

// socket
export const sendSocketHeart = () => {
	heartTime = setInterval(() => {
		let now = Date.now();
		// 
		sendMsg(JSON.stringify({
			action: "ping",
			data: now
		}));
		socketHeart = socketHeart + 1;
	}, heartTimeOut);
};

// socket
export const reconnect = () => {
	// 
	if (connectNum <= 3) {
		// closeSocketTask();
		clearInterval(heartTime);
		initWebSocket(socketUrl);
		connectNum = connectNum + 1;
		console.log('socket', connectNum);
		// uni.showLoading({
		// 	title: 'WebSocket,' + connectNum + ''
		// });
		uni.showLoading({
			// title: 'WebSocket,' + connectNum + ''
		});
		setTimeout(function() {
			uni.hideLoading();
		}, 2000);
	} else {
		console.log('');
		uni.hideLoading()
		clearInterval(heartTime);
		uni.showModal({
			title: '',
			content: '...',
			showCancel: false,
			confirmText: '',
			success: async res => {
				connectNum = 0;
				let user = uni.getStorageSync('user');
				const {
					data: token
				} = await refreshToken(user.id)
				console.log('token: ', token);
				uni.setStorageSync('token', token);
				initWebSocket(socketUrl);
			},
			fail: () => {},
			complete: () => {}
		});
	}
};

// socket 
export const sendMsg = (data) => {
	console.log(': ', data);
	socketTask.send({
		data: data,
		success(res) {
			// console.log("", res)
		},
		fail(err) {
			console.log("", err)
			reconnect();
		}
	});

};

// socket 
export const websocketonmessage = () => {
	socketTask.onMessage(function(res) {
		if (res.data.indexOf('heartbeat') != 0) {
			resetHeart();
		}
		if (res.data.indexOf('pong') != -1) {
			let now = Date.now();
			let before = res.data.split(',')[1];
			console.log(now - before + 'ms')
			uni.$emit("onPing", now - before);
			return;
		}
		uni.$emit("onMessage", res);
	});
};

//  socket
export const closeSocketTask = () => {
	if (null != socketTask) {
		socketTask.close();
	}
	clearInterval(heartTime);
}