import {
	http
} from '@/api/http.js'


/**
 * 
 */
export const register = (data) => {
	return http.request({
		url: 'user/register',
		method: 'POST',
		data
	});
}
/**
 * 
 */
export const login = (data) => {
	return http.request({
		url: 'user/login',
		method: 'POST',
		data
	});
}

// token
export const refreshToken = (userId) => {
	return http.request({
		url: 'user/refreshToken/' + userId,
		method: 'POST'
	})
}