import {
	http,
	baseURL,
	getUserTokenStorage
} from '@/api/http.js'
/**
 * 
 *  url 
 *
 */
export const getDataByDictType = (dictType) => {
	return http.request({
		url: 'system/getDataByDictType/' + dictType,
		method: 'GET'
	});
}

