import {
	accMul,
	accDiv
} from '@/utils/utils.js'
import {
	getDictLabelByValue
} from '@/api/dict.js'
export const filters = {
	// 
	formatMoney: value => {
		if (value == undefined) {
			return 0;
		}
		return accDiv(value, 100).toFixed(2);
	},
	// 
	formatShipTagType: value => {
		if (value == '0') {
			return 'success';
		}
		if (value == '1') {
			return 'primary';
		}
		if (value == '2') {
			return 'warning';
		}
	},
	// 
	formatOrderStatus: value => {
		if (value == '0') {
			// 
			return 'error';
		}
		if (value == '1') {
			// 
			return 'success';
		}
		if (value == '2') {
			// 
			return 'warning';
		}
		if (value == '3') {
			// 
			return 'primary';
		}
	},
	// 
	formatText: (value, dictData) => {
		if (value && value != '') {
			let shipmentTypeText = getDictLabelByValue(value, dictData);
			return shipmentTypeText;
		} else {
			return value;
		}

	},
	// id
	formatMaterialIds: ids => {
		if (ids && ids != '') {
			return ids.split(",").length + '';
		} else {
			return ids;
		}
	},
	// id
	formatEquipmentIds: ids => {
		if (ids && ids != '') {
			return ids.split(",").length + '';
		} else {
			return ids;
		}
	},
	// 0
	padZero: num => {
		if (num) {
			return num.toString().padStart(4, '0');
		}
	},
	formatSecondsToHMS: seconds => {
		return new Date(seconds * 1000).toISOString().substr(11, 8);
	}
}