import Request from 'luch-request'
import {
	refreshToken
} from '@/api/user.js'
import {
	baseIpPort
} from '@/static/json/base-url.js'

// const baseIpPort = "192.168.100.100:8083";
const baseURL = "http://" + baseIpPort + "/api/";
/* token */
const getUserTokenStorage = () => {
	let token = '';
	try {
		token = "Bearer " + uni.getStorageSync('token');
		// console.log(token);
	} catch (e) {}
	return token
}
/*****************************  *****************************/
const http = new Request()
http.setConfig((config) => {
	/*  */
	config.baseURL = baseURL /*  */
	config.header = {
		...config.header
	}
	config.custom = {
		auth: true, // token
		loading: false // loading
	}
	return config;
})


http.interceptors.request.use((config) => {
	/* async await  */
	// 
	if (config.custom.loading) {
		uni.showLoading();
	}
	config.header = {
		...config.header
	}
	// orgid
	// config.header.OrgId = getApp().vuex_org_id;
	if (config.custom.auth) {
		config.header.Authorization = getUserTokenStorage();
	}
	/*
 if (!token) { // tokenreturn Promise.reject(config) 
   return Promise.reject(config)
 }
 */
	// console.log('config: ', config);
	return config;
}, (config) => {
	return Promise.reject(config);
})

http.interceptors.response.use(async (response) => {
	/* async await   */
	if (response.data.code == 401) {
		console.error("401,tokentoken");
		// uni.setStorageSync('user', null);
		uni.setStorageSync('token', null);
		let user = uni.getStorageSync('user');

		if (user && user.id) {
			console.log('user: ', user);
			const {
				data: token
			} = await refreshToken(user.id)
			console.log('token: ', token);
			uni.setStorageSync('token', token);
			response.config.header.Authorization = 'Bearer ' + token
			return new Promise(resolve => {
				resolve(http.request(response.config))
			})
		} else {
			uni.setStorageSync('user', null);
			getApp().globalData.isLogin = false;
			uni.showModal({
				title: '',
				content: '',
				cancelText: '', // 
				confirmText: "", // 
				success(res) {
					if (res.confirm) {
						uni.reLaunch({
							url: '/pages/login/login'
						});
					}
				}
			})
			return Promise.reject(response);
			// console.error("");
			// uni.reLaunch({
			// 	url: "/pages/login/index"
			// })
			// }
			// 	count = 0
			// 	uni.setStorageSync('user', null);
			// 	setTimeout(() => {
			// 		try {
			// 			let pages = getCurrentPages() // 
			// 			// let currentPage = pages[pages.length - 1].route; // 
			// 			let currentPage = pages[pages.length - 1]['$page']['fullPath'] //()
			// 			if (currentPage.startsWith("pages")) {
			// 				currentPage = "/" + currentPage;
			// 			}
			// 			uni.reLaunch({
			// 				url: currentPage
			// 			})
			// 		} catch (e) {
			// 			//TODO handle the exception
			// 			uni.reLaunch({
			// 				url: '/pages/index/index'
			// 			})
			// 		}

			// 	}, 2000)
		}
	}
	if (response.data.code == 500) {
		// uni.showToast({
		// 	icon: 'error',
		// 	title: ''
		// })
		uni.showToast({
			icon: 'none',
			title: response.data.msg
		})
	}
	if (response.config.custom.loading) {
		uni.hideLoading()
	}
	return response;
}, (response) => { // async await 
	if (response.config.custom.loading) {
		uni.hideLoading()
	}
	if (response.statusCode == undefined) {
		uni.showToast({
			icon: 'none',
			title: ''
		})
	}
	return Promise.reject(response);
})

export {
	http,
	// baseIpPort,
	baseURL,
	getUserTokenStorage,
}